import React, { useRef, useEffect, useState } from "react";
import paper from "paper";
import "./styles.css";
import SimplexNoise from "simplex-noise";
import Transitions from "./Transitions";
import { useSpring, useSprings, useChain,animated } from "react-spring";
import styled from "styled-components";
import { menuList, cars_data, menu_items } from "./data";
import * as Icon from "react-feather";
import ShadeImage from './ShadeImage'
import img1 from "./images/img1.jpg";
import img2 from "./images/img2.jpg";
import img3 from "./images/img3.jpg";
import img4 from "./images/img4.jpg";





const menuItemHeight = 60;
const MenuItem = styled(animated.div)`
  box-sizing: border-box;
  cursor: pointer;
  fontfamily: "Helvetica";
  padding: "2rem";
  fontsize: "30px";

  transition: color 0.2s;
  :hover {
    color: blue;
  }
  :last-child {
    border-bottom: none;
  }
`;
MenuItem.defaultProps = {
  bg: "#fff",

};

const Menu = ({
  magnetIn,
  magnetOut,
  setRightMenuVisible,
  rightMenuVisible,
}) => {
  return (
    <div className="sidebar-items">
      <div className="menu-container">
        <input
          type="checkbox"
          class="menu-btn"
          id="menu-btn"
          onMouseEnter={magnetIn}
          onMouseLeave={magnetOut}
        />
        <label
          for="menu-btn"
          class="menu-icon"
          onMouseEnter={magnetIn}
          onMouseLeave={magnetOut}
          onClick={() => setRightMenuVisible(!rightMenuVisible)}
        >
          <span class="navicon"></span>
        </label>
      </div>
      <div className="current-item">MENU</div>
      <div className="arrow-containers">
        <button
          id="menu-wrapper"
          className="menu-wrapper"
          onMouseEnter={magnetIn}
          onMouseLeave={magnetOut}
        >
          <a href="#" class="link">
            <Icon.ArrowUp className="big_svg" />
          </a>
        </button>
        <button
          id="menu-wrapper"
          className="menu-wrapper"
          onMouseEnter={magnetIn}
          onMouseLeave={magnetOut}
        >
          <a href="#" class="link">
            <Icon.ArrowDown className="big_svg" />
          </a>
        </button>
      </div>
    </div>
  );
};

export default function Layout() {
  const mouse = useRef(null);
  const canvas = useRef(null);

  let lastX = 1000;
  let lastY = 1000;
  let clientX = 1000;
  let clientY = 1000;
  const [stuck, setStuck] = useState(false);
  const [[stuckX, stuckY], setStuckCoords] = useState([-100, -100]);
  const [fullMenuVisible, setFullMenuVisible] = useState(false);
  const [carIndex, setCarIndex] = useState(0);
  const [carData, setCarData] = useState(null);

  const magnetIn = (e) => {
    setStuck(true);
    const navItem = e.currentTarget;
    const navItemBox = navItem.getBoundingClientRect();
    setStuckCoords([
      Math.round(navItemBox.left + navItemBox.width / 2),
      Math.round(navItemBox.top + navItemBox.height / 2),
    ]);
    //stuckX = Math.round(navItemBox.left + navItemBox.width / 2);
    //stuckY = Math.round(navItemBox.top + navItemBox.height / 2);
    //console.log(navItem)
  };

  const magnetOut = () => {
    setStuck(false);
  };

  const handleMouseMove = (e) => {
    clientX = e.clientX;
    clientY = e.clientY;
    mouse.current.style.left = `${clientX - 2.5}px`;
    mouse.current.style.top = `${clientY - 2.5}px`;
    // console.log(stuck);
  };

  const initCanvas = () => {
    paper.setup(canvas.current);
    const polygon = new paper.Path.RegularPolygon(new paper.Point(0, 0), 8, 15);
    polygon.strokeColor = "rgb(128,198,56)";
    polygon.strokeWidth = 1;

    // polygon.smooth();
    const shapeBounds = {
      width: 75,
      height: 75,
    };

    const noiseScale = 150; // speed
    const noiseRange = 4; // range of distortion
    let isNoisy = false; // state

    const noiseObjects = polygon.segments.map(() => new SimplexNoise());
    let bigCoordinates = [];

    let group = new paper.Group([polygon]);
    group.applyMatrix = false;

    const lerp = (a, b, n) => {
      return (1 - n) * a + n * b;
    };

    const map = (value, in_min, in_max, out_min, out_max) => {
      return (
        ((value - in_min) * (out_max - out_min)) / (in_max - in_min) + out_min
      );
    };

    // if (clientX <= 70 && clientY <= 70) {
    //   isStuck = true;
    // } else {
    //   isStuck = false;
    // }

    paper.view.onFrame = (event) => {
      if (!stuck) {
        // move circle around normally
        lastX = lerp(lastX, clientX, 0.2);
        lastY = lerp(lastY, clientY, 0.2);
        group.position = new paper.Point(lastX, lastY);
      } else if (stuck) {
        lastX = lerp(lastX, stuckX, 0.2);
        lastY = lerp(lastY, stuckY, 0.2);
        group.position = new paper.Point(lastX, lastY);
      }

      if (stuck && polygon.bounds.width < shapeBounds.width) {
        // scale up the shape
        polygon.scale(1.08);
      } else if (!stuck && polygon.bounds.width > 30) {
        // remove noise
        if (isNoisy) {
          polygon.segments.forEach((segment, i) => {
            segment.point.set(bigCoordinates[i][0], bigCoordinates[i][1]);
          });
          isNoisy = false;
          bigCoordinates = [];
        }
        // scale down the shape
        const scaleDown = 0.92;
        polygon.scale(scaleDown);
      }
      /*  else {
        // console.log(lastX);
        lastX = lerp(lastX, 30, 0.3);
        lastY = lerp(lastY, 30, 0.3);
        group.position = new paper.Point(lastX, lastY);
      } */

      if (stuck && polygon.bounds.width >= shapeBounds.width) {
        isNoisy = true;
        // first get coordinates of large circle
        if (bigCoordinates.length === 0) {
          polygon.segments.forEach((segment, i) => {
            bigCoordinates[i] = [segment.point.x, segment.point.y];
          });
        }

        // loop over all points of the polygon
        polygon.segments.forEach((segment, i) => {
          // get new noise value
          // we divide event.count by noiseScale to get a very smooth value
          const noiseX = noiseObjects[i].noise2D(event.count / noiseScale, 0);
          const noiseY = noiseObjects[i].noise2D(event.count / noiseScale, 1);

          // map the noise value to our defined range
          const distortionX = map(noiseX, -1, 1, -noiseRange, noiseRange);
          const distortionY = map(noiseY, -1, 1, -noiseRange, noiseRange);

          // apply distortion to coordinates
          const newX = bigCoordinates[i][0] + distortionX;
          const newY = bigCoordinates[i][1] + distortionY;

          // set new (noisy) coodrindate of point
          segment.point.set(newX, newY);
        });
      }
      polygon.smooth();
    };
  };

  useEffect(() => {
    mouse.current.style.left = "-100px";
    mouse.current.style.top = "-100px";

    canvas.current.width = window.innerWidth;
    canvas.current.height = window.innerHeight;

    initCanvas();
  });

  const [rightMenuVisible, setRightMenuVisible] = useState(false);

  useEffect(() => {
    setCarData(cars_data[carIndex]);
  }, [carIndex]);

  const [open, set] = useState(true);
  const rightMenuAnimation = useSpring({
    /*  width: rightMenuVisible ? "100%" : "0%", */
    opacity: rightMenuVisible ? 1 : 0,
    transform: rightMenuVisible ? `translateX(0)` : `translateX(100%)`,
  });

  const springsRef = React.useRef();
  const delayValue = 100;
  const springs = useSprings(
    menuList.length,
    menuList.map((item, i) => ({
      ref: springsRef,
      item,
      delay: rightMenuVisible
        ? i * delayValue
        : menuList.length * delayValue - i * delayValue,
      opacity: rightMenuVisible ? 1 : 0,
      x: rightMenuVisible ? "0%" : "20%",
      from: {
        opacity: 0,
        x: "20%",
      },
    }))
  );

  const mainRef = React.useRef();

  const mainsprings = useSprings(
    carData ? carData.length : 2,
    carData
      ? carData.map((item, i) => ({
          ref: mainRef,
          item,
          delay: rightMenuVisible
            ? i * delayValue
            : cars_data[0].length * delayValue - i * delayValue,
          opacity: rightMenuVisible ? 0 : 1,
          y: rightMenuVisible ? "20%" : "0%",
          from: {
            opacity: 1,
            y: "0%",
          },
        }))
      : cars_data[0].map((item, i) => ({
          ref: mainRef,
          item,
          delay: rightMenuVisible
            ? cars_data[0].length * delayValue - i * delayValue
            : i * delayValue,
          opacity: rightMenuVisible ? 0 : 1,
          y: rightMenuVisible ? "20%" : "0%",
          from: {
            opacity: 1,
            y: "0%",
          },
        }))
  );

  const fullsprings = useSprings(
    menu_items.length,
    menu_items.map((item, i) => ({
      item,
      delay: fullMenuVisible
        ? i * delayValue
        : menu_items.length * delayValue - i * delayValue,
      opacity: fullMenuVisible ? 1 : 0,
      y: fullMenuVisible ? "0%" : "20%",
      from: {
        opacity: 0,
        y: "20%",
      },
    }))
  );

  const fullMenuAnimation = useSpring({
    transform: fullMenuVisible ? `translateY(0)` : `translateY(-100%)`,
    opacity: fullMenuVisible ? 1 : 0,
  });

  React.useEffect(() => {
    console.log(rightMenuVisible);
    setRightMenuVisible(false);
  }, []);

  const springRef = React.useRef();
  const { background, iconTransform, ...springProps } = useSpring({
    ref: springRef,

    iconTransform: open ? "rotate(0deg)" : "rotate(-45deg)",
    height: rightMenuVisible ? `${menuItemHeight * menuList.length}px` : "0px",
    from: {
      iconTransform: "rotate(-45deg)",
      height: "0px",
    },
  });

  useChain(
    rightMenuVisible ? [springsRef, mainRef] : [springsRef, mainRef],
    rightMenuVisible ? [0.75, 0] : [0.25, 0.25]
  );

  return (
    <>
      <div onMouseMove={handleMouseMove} className="container">

        <div ref={mouse} className="cursor cursor--small" />
        <canvas ref={canvas} className="cursor cursor--canvas" />

<Transitions/>
        <div
          className="main-content"
          style={{ width: "100%", height: "100%", position: "relative" }}
        >
          <div className="main-navbar">
            <a href="#" class="btn effect-1">
              IS
            </a>

            <div
              onMouseEnter={magnetIn}
              onMouseLeave={magnetOut}
              onClick={() => setFullMenuVisible(!fullMenuVisible)}
              className="collections__item"
            >
              <span>
                {" "}
                <Icon.Grid />
              </span>
              Our Collection
            </div>
          </div>
          <div className="main-info">
            {[mainsprings[0]].map(({ y, ...props }, i) => (
              <animated.div
                key={i}
                style={{
                  paddingBottom: "1rem",
                  fontSize: "62px",
                  fontWeight: "bold",
                  fontFamily: "Castoro",
                  transform: y.interpolate((y) => `translateY(${y})`),
                  ...props,
                }}
              >
                {props.item ? props.item : "Car Info Name"}
              </animated.div>
            ))}
            {[mainsprings[1]].map(({ y, ...props }, i) => (
              <animated.div
                key={i}
                style={{
                  paddingBottom: "1rem",
                  fontSize: "16px",
                  fontFamily: "Helvetica",
                  transform: y.interpolate((y) => `translateY(${y})`),
                  ...props,
                }}
              >
                {props.item ? props.item : "Car Info Name"}
              </animated.div>
            ))}

            {[mainsprings[1]].map(({ y, ...props }, i) => (
              <animated.div
                key={i}
                style={{
                  paddingBottom: "1rem",
                  letterSpacing: "2",
                  fontSize: "14px",
                  fontWeight: "bold",
                  textTransform: "uppercase",
                  fontFamily: "Montserrat",
                  transform: y.interpolate((y) => `translateY(${y})`),
                  ...props,
                }}
              >
                MORE
              </animated.div>
            ))}
          </div>

          <div className="main-footer">
            <span>
              {" "}
              <Icon.Instagram />
            </span>
          </div>
          <animated.div
            className="big__menu menu--full"
            style={fullMenuAnimation}
          >
            <div
              style={{
                height: "100%",
                width: "100%",
                overflow: "hidden",
                display: "flex",
                marginLeft: "2rem",
                marginTop: "2rem",
                flexDirection: "column",

               justifyContent: "center",
              }}
            >
              {fullsprings.map(({ y, ...props }, i) => (
                <MenuItem
                  key={i}
                  style={{
                    padding: "1rem",
                    fontSize: "32px",

                    fontFamily: "Castoro",
                    transform: y.interpolate((y) => `translateX(${y})`),
                    ...props,
                  }}
                  magnetIn={magnetIn}
                  magnetOut={magnetOut}
                >
                  {props.item}
                </MenuItem>
              ))}
            </div>
            <div className="newsletter__container">
              <div className="newsletter__images">
                <img className="newsletter__image" src={img1} alt="img1" />
                <img className="newsletter__image" src={img2} alt="img2" />
                <img className="newsletter__image" src={img3} alt="img3" />
                <img className="newsletter__image active" src={img4} alt="img4" />
              </div>

              <div className="newsletter__clicker">
                <div
                  className="newsletter__circle"
                  magnetIn={magnetIn}
                  magnetOut={magnetOut}
                ></div>
                <div
                  className="newsletter__circle"
                  magnetIn={magnetIn}
                  magnetOut={magnetOut}
                ></div>
                <div
                  className=" active newsletter__circle"
                  magnetIn={magnetIn}
                  magnetOut={magnetOut}
                ></div>
              </div>

              <div className="newsletter__footer">
                <input
                placeholder="NEWSLETTER"
                  className="newsletter__input"
                  type="text"
                  id="lname"
                  name="lname"
                />
              </div>
            </div>
          </animated.div>
          <animated.div className="menu menu--right" style={rightMenuAnimation}>
            <div className="menu--image">
           <ShadeImage/>
            </div>
            <div
              style={{
                height: "100vh",
                width: "100%",
                overflow: "hidden",
                display: "grid",
                gridTemplateRows: '1fr 50px',
                background: "#fff",
                padding: "2rem",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "flex-start",
              }}
            >
            <div style={{      height: "100%", width: "100%", display: "flex", flexDirection: "column",
                justifyContent: "center",
                alignItems: "flex-start"}}>
              {springs.map(({ x, ...props }, i) => (
                <MenuItem
                  key={i}
                  style={{
                    fontSize: "36px",
                    paddingTop: '2rem',
                    paddingBottom: '2rem',
                    paddingLeft: '1rem',
                    fontFamily: "Castoro",
                    transform: x.interpolate((x) => `translateX(${x})`),
                    ...props,
                  }}
                  magnetIn={magnetIn}
                  magnetOut={magnetOut}
                >
                  {props.item}
                </MenuItem>
              ))}
              </div>
              <div style={{display: 'flex', flexDirection: 'row', width: '100%',   paddingBottom: "1rem",
                  letterSpacing: "2",
                  fontSize: "14px",
                  fontWeight: "bold",
                  textTransform: "uppercase",
                  fontFamily: "Montserrat",  alignItems: 'center', justifyContent: 'flex-start'}}>
                  <div style={{paddingLeft: '1rem', marginRight: '2rem'}}>Hello</div>
                  <div style={{marginRight: '2rem'}}>Goodbye</div>
                  
              </div>
            </div>
          </animated.div>
        </div>

        <div className="sidebar">
          <Menu
            magnetIn={magnetIn}
            magnetOut={magnetOut}
            setRightMenuVisible={setRightMenuVisible}
            rightMenuVisible={rightMenuVisible}
          />
        </div>
      </div>
    </>
  );
}
